@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --principal-green: hsl(183, 100%, 15%);
  --grayish-cyan: hsl(186, 14%, 43%);
  --hover-green: hsl(185, 41%, 84%);
  --selected-green: hsl(172, 67%, 45%);
  --input-background: hsl(189, 41%, 97%);
  --error-color: hsl(16, 100%, 47%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Space Mono", monospace;
}

.App {
  min-height: 100vh;
  background-color: hsl(185, 41%, 84%);
  background-color: var(--hover-green);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.splitter-container {
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 7px;
  color: hsl(183, 100%, 15%);
  color: var(--principal-green);
  margin: 2.5rem;
  margin-bottom: 2rem;
}

form {
  max-width: 860px;
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 3rem;
  -webkit-column-gap: 3rem;
          column-gap: 3rem;
  padding: 1.5rem;
  background-color: white;
  border-radius: 30px 30px 0px 0px;
}

input {
  border: none;
}

@media (min-width: 768px) {
  .App {
    padding: 0 15%;
  }

  form {
    grid-template-columns: 1fr 1fr;
    border-radius: 15px;
  }
}

.inputSection-container {
    flex: 1.3 1;
    margin: 1rem 0;
}

.inputSection-container > *:not(:last-child) {
    margin-bottom: 2.5rem;
}
.label-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.label-container > :not([class = 'label']) {
    color: var(--error-color);
}

.label{
    color: var(--grayish-cyan);
}
:root{
    --padding-vertical: 0.3rem;
    --padding-horizontal: 1rem;
}

.InputElement-container {
    position: relative;
    background-color: hsl(189, 41%, 97%);
}

.InputElement-container > * {
    background-color: inherit;
}

.icon {
    position: absolute;
    left: 1rem;
    left: var(--padding-horizontal);
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.input {
    width: 100%;
    text-align: right;
    padding: 0.3rem;
    padding: var(--padding-vertical);
    padding-right: 1rem;
    padding-right: var(--padding-horizontal);
    color: var(--principal-green);
    font-weight: 700;
    font-size: 1.3rem;
    border-radius: 4px;
}

.input {
    cursor: pointer;
}

input:focus {
    outline-color: var(--selected-green);
}

.error:focus {
    outline-color: var(--error-color);
}

.tipElements-container {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 1rem;
}

@media (min-width: 768px){
    .tipElements-container {
        grid-template-columns: repeat(3, minmax(0, 1fr));
        grid-gap: 0.8rem;
    }
}
.tipElement-container{
    background-color: var(--principal-green);
    color: white;
    border-radius: 4px;
    text-align: center;
    padding: 0.3rem;
    font-weight: 600;
    font-size: 1.3rem;
}

.tipElement-container:hover {
    cursor: pointer;
}

.tipElement-container:not([class ~= selected]):hover  {
    background-color: var(--hover-green);
    color: var(--principal-green);
}

.selected{
    color: var(--principal-green);
    background-color: var(--selected-green);
}
.customTip-container > input {
    text-align: right;
    width: 100%;
    box-sizing: border-box;
    padding: 0.3rem;
    padding-right: 1rem;
    font-weight: 600;
    font-size: 1.3rem;
    color: var(--principal-green);
}

.customTip-container > input:hover {
    cursor: pointer;
}
.resultSection-container{
    background-color: var(--principal-green);
    border-radius: 12px;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1 1;
    padding: 2rem;
}

.reset {
    margin-top: 2rem;
    padding: 0.6rem;
    font-size: 1.2rem;
    font-weight: 600;
    border-radius: 5px;
    border: none;
    color: var(--principal-green); 
}

.reset:not(:disabled){
    background-color: var(--selected-green);
}

.reset:not(:disabled):hover  {
    cursor: pointer;
    background-color: var(--hover-green);
}
.resultElement-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.3rem;
}

.resultLabel-container {
    display: flex;
    flex-direction: column;
}

.resultLabel {
    color: white;
}

.person {
    color: var(--grayish-cyan);
    font-size: 0.9rem;
}

.amount {
    color: var(--selected-green);
    font-size: 3rem;
}

@media (min-width: 768px){
    .resultElement-container {
        margin-bottom: 1rem;
    }
}
